import React, { useState } from 'react';
import './App.css';
import { useSession, useSupabaseClient, useSessionContext } from '@supabase/auth-helpers-react';
import DateTimePicker from 'react-datetime-picker';

function App() {
  const [start, setStart] = useState(new Date());
  const [end, setEnd] = useState(new Date());
  const [eventName, setEventName] = useState("");
  const [eventDescription, setEventDescription] = useState("");
  const [textToProcess, setTextToProcess] = useState("");

  const session = useSession();
  const supabase = useSupabaseClient();
  const { isLoading } = useSessionContext();

  async function googleSignIn() {
    const { error } = await supabase.auth.signInWithOAuth({
      provider: 'google',
      options: {
        scopes: 'https://www.googleapis.com/auth/calendar'
      }
    });
    if (error) {
      console.log(error);
      alert("Error logging in to Google provider with Supabase");
    }
  }

  async function signOut() {
    await supabase.auth.signOut();
  }

  async function createCalendarEvent(summary, startDateTime, endDateTime) {
    if (isNaN(startDateTime) || isNaN(endDateTime)) {
      console.error("Invalid date provided for event:", summary);
      return;
    }

    console.log("Creating calendar event");
    const event = {
      'summary': summary,
      'start': {
        'dateTime': startDateTime.toISOString(),
        'timeZone': Intl.DateTimeFormat().resolvedOptions().timeZone
      },
      'end': {
        'dateTime': endDateTime.toISOString(),
        'timeZone': Intl.DateTimeFormat().resolvedOptions().timeZone
      }
    };
    await fetch("https://www.googleapis.com/calendar/v3/calendars/primary/events", {
      method: "POST",
      headers: {
        'Authorization': 'Bearer ' + session.provider_token
      },
      body: JSON.stringify(event)
    }).then((data) => {
      return data.json();
    }).then((data) => {
      console.log(data);
      alert("Event created, check your Google Calendar!");
    });
  }

  async function sendTextAndCreateEvent() {
    try {
      const response = await fetch('http://localhost:5000/process-text', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({ text: textToProcess })
      });
      if (response.ok) {
        const events = await response.json();
        console.log("Received events:", events);

        events.forEach(event => {
          console.log("Event date time:", event.date_time);
          const startDateTime = new Date(event.date_time);
          console.log("Converted start date:", startDateTime);

          if (!isNaN(startDateTime)) {
            createCalendarEvent(event.event, startDateTime, startDateTime); // Assuming end time is the same as start time
          } else {
            console.error("Invalid date for event:", event.event);
          }
        });
        alert("Events processed and added to Google Calendar!");
      } else {
        console.error('Error from backend');
      }
    } catch (error) {
      console.error('Error:', error);
    }
  }

  if (isLoading) {
    return <div>Loading...</div>
  }

  return (
    <div className="App">
      <div style={{width: "400px", margin: "30px auto"}}>
        {session ?
          <>
            <h2>Hey there {session.user.email}</h2>
            <p>Enter text to process</p>
            <textarea 
              value={textToProcess}
              onChange={(e) => setTextToProcess(e.target.value)}
              style={{ width: '100%', marginBottom: '10px' }}
            />
            <button onClick={sendTextAndCreateEvent}>Process Text and Create Event</button>
            <hr />
            <button onClick={() => signOut()}>Sign Out</button>
          </>
          :
          <button onClick={() => googleSignIn()}>Sign In With Google</button>
        }
      </div>
    </div>
  );
}

export default App;
